
import './App.css';
import Navbar from './components/Navbar';
import Footer from './components/Footer';
import TextForm from './components/TextForm';
import About from './components/About';
import { useState } from 'react';
import Alert from './components/Alert';

function App() {
  const [mode, setMode] = useState('light');
  const [alert,setAlert] = useState(null);
  const showAlert = (message,type)=>{
      setAlert({
        msg : message,
        type : type
      })
      setTimeout(()=>{
        setAlert(null);
      },2500);
  }
  const toggleMode = ()=>{
    // console.log("toggle");
    // console.log(mode);
    if(mode==='light'){
      setMode('dark');
      showAlert("Dark Mode Enabled","Success");
    }
    else{
      setMode('light');
      showAlert("Light Mode Enabled","Success");
    }
  }
  return (
    <>
    
    <Navbar title="Play With Text" mode={mode} toggleMode={toggleMode} aboutText="About"/>
    <div className="container" style={{backgroundColor : mode==='dark'?'grey':'white'}}>
      
    <Alert alert={alert}/>
    <ul class="nav nav-tabs" id="myTab" role="tablist">
        <li class="nav-item" role="presentation">
          <button
            class="nav-link active"
            id="home-tab"
            data-bs-toggle="tab"
            data-bs-target="#home-tab-pane"
            type="button"
            role="tab"
            aria-controls="home-tab-pane"
            aria-selected="true"
          >
            Home
          </button>
        </li>
        <li class="nav-item" role="presentation">
          <button
            class="nav-link"
            id="profile-tab"
            data-bs-toggle="tab"
            data-bs-target="#profile-tab-pane"
            type="button"
            role="tab"
            aria-controls="profile-tab-pane"
            aria-selected="false"
          >
            About
          </button>
        </li>
      </ul>
      <div class="tab-content" id="myTabContent">
        <div
          class="tab-pane fade show active"
          id="home-tab-pane"
          role="tabpanel"
          aria-labelledby="home-tab"
          tabindex="0"
        >
          <TextForm heading="Change Case | Count Words | Count Spaces | Remove Extra Spaces | Text to Speech | Speech to Text" mode={mode} showAlert={showAlert}/>
        </div>
        <div
          class="tab-pane fade"
          id="profile-tab-pane"
          role="tabpanel"
          aria-labelledby="profile-tab"
          tabindex="0"
        >
          <About/>
        </div>
      </div>
      
      {/* <TextForm heading="Change Case | Count Words | Count Spaces | Remove Extra Spaces | Text to Speech | Speech to Text" mode={mode} showAlert={showAlert}/>
           */}
    </div>
    <Footer/>   
    </>
  );
}

export default App;
